import React from "react";
import style from "./Main.module.css"
import {AboutMe} from "./AboutMe/AboutMe";

export const Main = () => {
    return (
        <div className={style.mainBlock}>

            <div className={style.mainHeader}>
                <h1>About me</h1>
                <p>Frontend-developer</p>
            </div>

            <div className={style.mainContainer}>

                <div className={style.photo}>

                </div>

                <div className={style.text}>

                    <h2>Фамилия Имя</h2>
                    <h4>I like programming to ReactJS CSS etc</h4>
                    <p>
                        Lorem ipsum — классический текст-«рыба». Является искажённым отрывком из философского трактата
                            Марка Туллия Цицерона «О пределах добра и зла»,
                            написанного в 45 году до н. э. на латинском языке, обнаружение сходства приписывается
                        Ричарду МакКлинтоку.
                    </p>
                    <div className={style.meInfo}>
                        <AboutMe title={"Birthday"} title2={"Residence"} title3={"Address"}
                                 value={"10.20.2021"} value2={"Russia"} value3={"Moscow"}/>
                        <AboutMe title={"E-mail"} title2={"Phone"} title3={"Telegram"}
                                 value={"blablabla@gmail.com"} value2={"8(800)555-35-35"} value3={"@name"}/>

                    </div>
                </div>
            </div>


        </div>
    )
}